<template>
  <div id="home">
    <div id="card-block" class="md-display-3">
      <template v-for="user in userData">
        <image-card :username="user.username" :imageUrl="user.imageUrl"></image-card>
      </template>
    </div>
  </div>
</template>

<script>
import ImageCard from '../components/ImageCard'
import gql from 'graphql-tag'

let imagesQuery = gql`
  query {
    allImages {
      edges {
        node {
          url
          description
        }
      }
    }
  }
`
export default {
  name: 'Home',
  components: {ImageCard},
  data: () => ({
    userData: [],
    loading: 0
  }),
  apollo: {
    userData: {
      query: imagesQuery,
      loadingKey: 'loading'
    }
  }
}
</script>

<style>
  #home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  #card-block {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
</style>


