<template>
  <md-card md-with-hover>
    <md-card-header>
      <router-link :to="`/detail/${username}`">
        <md-avatar>
          <img src="http://placehold.it/50x50" alt="thumbnail">
        </md-avatar>
      </router-link>

      <div class="md-title">{{imageDescription}}</div>
      <div class="md-subhead">{{username}}</div>
    </md-card-header>

    <md-card-media>
      <img src="http://placehold.it/192x108" alt="Image">
    </md-card-media>

    <md-card-actions>
      <md-button>Action</md-button>
      <md-button>Action</md-button>
    </md-card-actions>
  </md-card>
</template>

<script>
  export default {
    name: 'UserCard',
    props: {
      imageUrl: {
        type: String,
        required: true
      },
      username: {
        type: String,
        required: true
      },
      profilePicture: String,
      imageDescription: String
    }
}
</script>

<style scoped>
  .md-with-hover {
    margin: 20px 10px;
  }
</style>


