<template>
  <div id="detail">
    <template v-for="user in userData">
      <image-card :username="user.username" :imageUrl="user.imageUrl"></image-card>
    </template>
    <router-link to="/">
      <md-button class="md-raised">Home View</md-button>
    </router-link>
  </div>
</template>

<script>
import ImageCard from '../components/ImageCard'
import api from '../data.js'
export default {
  name: 'Detail',
  components: {ImageCard},
  data: () => ({
    userData: []
  }),
  created: function fetchUserData () {
    this.userData = api.users.filter(el => el.username === this.$route.params.username)
  }
}
</script>

<style>
  #detail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
</style>


